/* ------------------------------------------------------------ *\
	Project Tiles
\* ------------------------------------------------------------ */
.project-tiles {
	margin: 0;
}

.project-tiles .project-tile {
	padding: 0;
	overflow: hidden;
}

.project-tile-inner {
	position: relative;
	height: 0;
	padding-bottom: 63.636%;
}

.project-tile-image {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.project-tile-image:after {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: $dark-grey;
	visibility: hidden;
	opacity: 0;
}

.project-tile:hover .project-tile-image:after {
	visibility: visible;
	opacity: 0.9;
}

.project-tile-overlay {
	position: absolute;
	left: 0;
	width: 100%;
	bottom: -50px;
	z-index: 10;
	text-align: center;
}

.project-tile-overlay .button.tiny {
	width: 190px;
	font-size: 13px;
	margin-bottom: 0;
	padding-top: 13px;
}

.project-tile:hover .project-tile-overlay {
	bottom: 50%;
	margin-bottom: -20px;
}

.project-tile:hover .project-tile-overlay h4 {
	color: $white;
}

.project-tile:hover .project-tile-overlay {
	transform: translateY(50%);
}

.project-tile-overlay h4 {
	color: $white;
	font-size: 14px;
	font-weight: bold;
	text-transform: uppercase;
	margin: 0 0 6px;
}

.project-tile-overlay p {
	color: $grey;
	font-size: 13px;
	text-transform: uppercase;
}

.project-tile-image:after,
.project-tile-overlay {
	transition: all 0.2s ease-out;
}

.section-project .section-body {
	margin-bottom: 0;
}

.single-post_portfolio .post-body {
	margin-top: 40px;
}

.single-post_portfolio .slide-image img {
	width: 100%;
	height: auto;
}

.portfolio-options {
	font-size: 13px;
	text-transform: uppercase;
	font-weight: 300;
	color: $grey;
	margin: 20px 0 30px;

	span {
		margin: 0 5px;
	}

	strong {
		color: $black;
	}
}
