/* ------------------------------------------------------------ *\
	Shortcodes
\* ------------------------------------------------------------ */
.row.grey {
	color: $grey;
}
/* ------------------------------------------------------------ *\
  Headline
\* ------------------------------------------------------------ */
.section-default-title.inside-content:after {
	margin-bottom: 20px!important;
}

.section-default-title.inside-content {
	margin-bottom: 20px;
}
/* ------------------------------------------------------------ *\
	Heading Block
\* ------------------------------------------------------------ */
.shortcode_heading_block {
	width: 100%;
	height: auto;
	padding: 60px 8%;
	text-align: center;
	background-size: cover !important;
	background-position: center center !important;
	background-repeat: no-repeat !important;
	margin-bottom: 30px;

	h1 {
		margin: 0;
		font-size: 200%;
		line-height: 130%;
	}
}
/* ------------------------------------------------------------ *\
	Text Block
\* ------------------------------------------------------------ */
.shortcode_text_block {
	width: 100%;
	height: auto;
	padding: 40px 20px;
	text-align: center;
	background-size: cover !important;
	background-position: center center !important;
	background-repeat: no-repeat !important;

	.title {
		font-weight: 700;
		padding: 0 5%;
		margin: 0 0 24px;
		line-height: 130%;
	}

	p {
		padding: 20px 5% 0 !important;
		margin: 0 !important;
		line-height: 200%;
		font-size: 100%;
	}

	.text_block_sep {
		width: 100px;
		height: 2px;
		background: $white;
		margin: 0 auto;
	}
}
/* ------------------------------------------------------------ *\
	Text Columnes
\* ------------------------------------------------------------ */
.shortcode_text_columns {
	color: $grey;
	column-gap: 48px;
	margin-bottom: 2em;

	&.two-cols {
		column-count: 2;
	}

	&.three-cols {
		column-count: 3;
	}

	&.four-cols {
		column-count: 4;
	}
}
/* ------------------------------------------------------------ *\
	Service Table
\* ------------------------------------------------------------ */
.shortcode_service_table_holder {
	display: block;
	position: relative;
}

.shortcode_service_table_inner {
	display: block;
	position: relative;
	list-style: none;
	margin: 0;
	padding: 0 12px 12px;
}

.shortcode_service_table_inner ul {
	padding: 0 !important;
}

.shortcode_service_table_inner li {
	display: block;
	margin: 0;
	padding: 0;
	list-style: none;
	text-align: center;
}

.shortcode_service_table_inner li li {
	padding: 12px;
	margin: 0 24px;
	border-bottom: 1px solid rgba(64,64,64,0.15);
}

.shortcode_service_table_inner li ul {
	margin-bottom: 0;
	padding-bottom: 0;
}

.shortcode_service_table_inner li:last-child {
	border-bottom: 0;
}

.shortcode_service_table_inner li.title_holder {
	display: block;
	position: relative;
	padding: 12px 20px;
	border: none;
}

.shortcode_service_table_inner li.title_holder i {
	color: #00a9c5;
}

.shortcode_service_table_holder .title_inner {
	position: relative;
	display: table;
	width: 100%;
	height: 100%;
	text-align: center;
}

.shortcode_service_table_holder .service_title {
	display: block;
}

.shortcode_service_table_holder .title_holder i {
	display: block;
	line-height: 1em;
	padding: 24px 0 0;
}
/* ------------------------------------------------------------ *\
	Pricing Tables
\* ------------------------------------------------------------ */
.shortcode_price_table_holder {
	position: relative;
	display: inline-block;
	width: 100%;
}

.shortcode_price_table_holder .shortcode_price_table {
	position: relative;
	display: block;
	float: left;
	padding: 35px 0 0;
}

.price_table_inner,
.shortcode_price_table .active_price_table {
	position: relative;
	display: block;
	margin: 0 12px;
}

.shortcode_price_table .active_price_table {
	text-align: center;
	margin: -35px 12px 0;
	background-color: #00a9c5;
}

.shortcode_price_table_holder .active_price_table h5 {
	margin: 0;
	padding: 0;
	font-size: 12px;
	color: #fff;
	line-height: 35px;
}

.price_table_inner {
	background: #f7f7f7;
}

.price_table_inner ul,
.price_table_inner ul ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.price_table_inner ul li {
	text-align: center;
	margin: 0;
	padding: 0 24px;
}

.price_table_inner ul li.pricing_table_content > ul {
	padding: 0 0 12px;
}

.price_table_inner ul li.pricing_table_content > ul > li {
	line-height: 24px;
	padding: 12px 0;
	margin: 0 24px;
	border-bottom: 1px solid rgba(64,64,64,0.15);
}

.price_table_inner ul li.pricing_table_content > ul > li:last-child,
.shortcode_price_table_holder .shortcode_price_table.active_price .price_table_inner ul li.pricing_table_content > ul > li:last-child {
	border-bottom: none;
}

.price_table_inner ul li.prices {
	padding: 0 12px;
}

.price_table_inner .title {
	text-transform: uppercase;
	margin: 0;
	padding: 32px 0 0;
}

.price_table_inner .subtitle {
	font-size: 16px;
	padding: 0;
}

.price_table_inner .price_in_table,
.price_table_inner .title {
	display: block;
}

.price_table_inner .price_in_table {
	padding: 18px 0 12px;
	color: #1c1c1e;
}

.price_in_table .mark,
.price_in_table .value {
	font-size: 16px;
	line-height: 32px;
	vertical-align: top;
	display: inline-block;
}

.price_in_table .mark {
	position: relative;
	font-size: 21px;
	letter-spacing: 1px;
	vertical-align: bottom;
}

.price_in_table .price {
	position: relative;
	display: inline-block;
	font-size: 42px;
	font-weight: 700;
	line-height: 36px;
}

.price_table_inner ul li .button {
	margin: 8px auto;
}

.shortcode_price_table_holder.one_column .shortcode_price_table {
	float: none;
}

.shortcode_price_table_holder.two_columns .shortcode_price_table {
	width: 50%;
}

.shortcode_price_table_holder.three_columns .shortcode_price_table {
	width: 33.3%;
}

.shortcode_price_table_holder.four_columns .shortcode_price_table {
	width: 25%;
}

.shortcode_price_table_holder.five_columns .shortcode_price_table {
	width: 20%;
}
/* ------------------------------------------------------------ *\
	Padding
\* ------------------------------------------------------------ */
.shortcode_padding {
	padding: 3%;
}
/* ------------------------------------------------------------ *\
	Highlight
\* ------------------------------------------------------------ */
.shortcode_highlight {
	background-color: #fcfca8;
	padding: 4px;
}
