/* ------------------------------------------------------------ *\
	Footer
\* ------------------------------------------------------------ */
.footer {
	position: relative;
	z-index: 100;
}

.footer-body {
	background: $white;
	color: $grey;
	overflow: hidden;
}

.footer-bar {
	background: $black;
	color: $grey;
	padding: 28px 0 22px;
}
/*  Footer Sections  */
.footer-section {
	position: relative;
	padding: 51px 0 34px;

	&-alt:before {
		content: '';
		position: absolute;
		right: 0;
		top: 0;
		width: 4000px;
		height: 1000px;
		background: $dark-orange;
	}

	.footer-section-body,
	.footer-section-head {
		position: relative;
		z-index: 10;
	}

	.footer-section-title {
		font-size: 19px;
		color: $dark-grey;
		font-weight: bold;
	}

	&-alt .footer-section-title {
		color: $dark-grey;
		font-size: 19px;
	}

	.footer-section-title:after {
		content: '';
		display: block;
		width: 60px;
		height: 1px;
		margin: 16px 0 22px;
		background: $dark-orange;
	}

	&-alt .footer-section-title:after {
		background: $white;
		margin-top: 15px;
	}

	.footer-section-entry p {
		font-size: 15px;
		line-height: 1.7;
	}

	.footer-section-entry p + p {
		margin-top: 25px;
	}
	@media only screen and (max-width: 1025px) {
		& {
			padding: 30px 0 20px;
		}

		&-alt:before {
			height: 100%;
			right: -1000px;
		}
	}
}
/*  Footer Bar  */
.footer-bar {
	.copyright {
		font-size: 13.5px;
		margin: 0;
	}

	.copyright a:hover {
		color: $dark-orange;
	}

	.footer-nav ul {
		display: inline-block;
		list-style: none outside none;
		overflow: hidden;
		margin: 0;
	}

	.footer-nav li {
		float: left;
		font-size: 13.5px;
	}

	.footer-nav li + li {
		margin-left: 19px;
	}

	.footer-nav a {
		color: $grey;
	}

	.footer-nav a:hover {
		color: $blue;
	}
	@media only screen and (max-width: 1025px) {
		& {
			text-align: center;
		}

		.left,
		.right {
			float: none !important;
		}

		.copyright {
			margin-bottom: 10px;
		}

		.footer-nav li {
			float: none;
			display: inline-block;
			vertical-align: top;
			margin: 0 9px;
		}
	}
}
