@import 'flexslider/_flexslider';
/* ------------------------------------------------------------ *\
	Slider Defaults
\* ------------------------------------------------------------ */
.flex-direction-nav a.flex-next:before,
.flex-direction-nav a:before {
	content: none;
}

.flex-direction-nav a {
	opacity: 1;
}
/* ------------------------------------------------------------ *\
	Slider Home
\* ------------------------------------------------------------ */

.no-js.slider-home {
	display: none;
}
.slider-home {
	position: relative;

	&.loading:after {
		content: '';
		position: absolute;
		z-index: 1000;
		left: 50%;
		top: 50%;
		width: 48px;
		height: 48px;
		background: $white url(assets/images/ajax-loader.gif) no-repeat center center;
		border: 3px solid rgba(255, 255, 255, 0.5);
		border-radius: 3px;
		background-clip: content-box;
	}

	.flex-control-paging {
		display: none;
	}

	.flex-direction-nav {
		position: absolute;
		z-index: 100;
		left: 0;
		top: 50%;
		width: 100%;
		list-style: none outside none;
		margin: 0;
	}

	.flex-direction-nav a {
		position: absolute;
		width: 60px;
		height: 60px;
		margin-top: -12px;
		background: $white;
		box-sizing: content-box;
		background-clip: content-box;
		font-size: 31px;
		color: $dark-orange;
		line-height: 60px;
		text-align: center;
	}

	.flex-direction-nav a:hover {
		background: $dark-orange;
		color: $white;
	}

	.flex-prev {
		left: 0;
	}

	.flex-next {
		right: 0;
	}

	.slides {
		list-style: none outside none;
		margin: 0;
		height: auto !important;
	}

	.slide {
		position: relative;
		overflow: hidden;
		height: auto !important;
	}
	// .slides:after,
	.slide:after {
		content: '';
		display: block;
		padding-top: 35.92%;
	}

	.slide-image {
		position: absolute;
		z-index: 10;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}

	.slide-body {
		position: absolute;
		z-index: 100;
		left: 50%;
		top: 50%;
		text-align: center;
		margin-top: 20px;
	}

	.slide-body {
		transform: translate(-50%, -50%);
	}

	.slide-title span {
		color: #fff!important;
		background: rgba(33, 33, 33, 0.3);
		padding: 11px 20px;
		margin: 10px 20px;
		display: inline-block;
	}

	.slide-caption span {
		color: #fff!important;
		background: rgba(33, 33, 33, 0.3);
		padding: 11px 20px;
		margin: 0 20px;
		display: inline-block;
	}

	.slide-actions .button.tiny {
		padding: 13px 11px;
		margin: 10px 20px;
	}
	@media only screen and (max-width: 1399px) {
		.flex-direction-nav a {
			width: 30px;
			height: 30px;
			line-height: 30px;
			font-size: 24px;
		}
	}
	@media only screen and (max-width: 641px) {
		.slide {
			height: 300px !important;
		}

		.slide-body {
			width: 80%;
		}
	}
}
/* ------------------------------------------------------------ *\
	Post Slider
\* ------------------------------------------------------------ */
.post-slider {
	position: relative;

	&.loading:after {
		max-height: 300px;
		content: '';
		position: absolute;
		z-index: 12000;
		left: 50%;
		top: 50%;
		width: 48px;
		height: 48px;
		background: $white url(assets/images/ajax-loader.gif) no-repeat center center;
		border: 3px solid rgba(255, 255, 255, 0.5);
		border-radius: 3px;
		background-clip: content-box;
	}

	.flex-control-paging {
		display: none;
	}

	.flex-direction-nav {
		position: absolute;
		z-index: 200;
		right: 12px;
		bottom: 4px;
		list-style: none outside none;
	}

	.flex-direction-nav li {
		float: left;
		margin-left: 12px;
	}

	.flex-direction-nav a {
		width: auto;
		position: static;
		display: block;
	}

	.flex-direction-nav a:focus {
		outline: none;
	}

	.flex-direction-nav .button {
		width: 34px;
		height: 34px;
		margin-bottom: 0;
		line-height: 30px;
		padding: 0;
		text-align: center;
		font-size: 20px;
	}

	.slides {
		list-style: none outside none;
		margin: 0;
	}
}
